.navbar {
  display: flex;
  position: absolute;
  color: white;
  flex-direction: row;
  justify-content: space-between;
}

.homeLink img {
  width: 35px;
  height: 35px;
  position: relative;
  left: 40px;
  top: 12px;
  -webkit-transition: -webkit-transform 0.8s ease-in-out;
  transition: transform 0.8s ease-in-out;
}

.homeLink img:hover {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
}

.homeLink div {
  display: inline-block;
  margin-left: 1.75rem;
  color: #fff;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 550;
  font-size: larger;
}

.links {
  display: flex;
  flex-direction: row;
  margin-left: 1100px;
}

.links p {
  margin: 20px;
  color: #fff;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 550;
}

.links p:hover {
  color: #bbc1c8;
  transform: translateY(-0.25rem);
}
